@use '../abstract/breakpoints' as *;
@use '../abstract/colors' as *;
@use "../abstract/fonts" as *;
@use '../abstract/functions' as *;
@use '../abstract/mixins' as *;


.form{
    &__group{
        margin-bottom: rem(16);
        position: relative;
        @include flex-center-vertical;
        @include border-radius(8px);
    }
    &__control{
        width: 100%;
        background: $clr-white;
        border:none;
        outline:1px solid $clr-primary;
        padding: em(16);
        color:$clr-primary;
        font-size: rem(16);
        font-weight: 600;
        line-height: 2;
        font-family: $ff-secondary;
        @include border-radius(4px);
        @include breakpoint-md{
            padding: em(16) em(20);
        }
        &:focus, &:hover{
            outline-color: $clr-secondary;  
        }
    }

    &__label{
        pointer-events: none;
        position: absolute;
        top: 50%;
        left: 10px;
        font-family: $ff-secondary;
        font-size: 18px;
        line-height: 28px;
        text-transform: capitalize;
        padding-left: 1em;
        transform: translateY(-50%);
        transition: all .3s ease;
     
        @include breakpoint-lg{
            font-size: 20px;
        }
    }

}


.form-group-wrapper{
    position: relative;
    @include flex-center-vertical;
    padding:8px;
    @include border-radius(8px);
    background: $clr-white;

    // .form__label{
    //     pointer-events: none;
    //     position: absolute;
    //     top: 50%;
    //     left: 10px;
    //     font-family: $ff-secondary;
    //     font-size: 18px;
    //     line-height: 28px;
    //     text-transform: capitalize;
    //     padding-left: 1em;
    //     transform: translateY(-50%);
    //     transition: all .3s ease;
     
    //     @include breakpoint-xl{
    //         font-size: 20px;
    //     }
    // }

    .form__control{
        outline: none;
        padding-block:0;
    }
}

.footer-form-control{
    line-height: 3;
}

.form__control:focus + label,
.form__control:valid + label{
    top: 18px;
    font-size: rem(16);
}

.form__control:focus + label{
    color:$clr-secondary;
}


